<template>
  <div class="main">
    <HeaderMenu></HeaderMenu>
    <Spinner></Spinner>

    <div class="main__block">
      <div class="main__block-title">ВИДЕО</div>
      <div class="main__line"></div>
      <div class="main__item-block">
        <div class="main__video-block-item">
          <iframe width="100%" height="315" src="https://www.youtube.com/embed/LAENAVwtgB8" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          <div class="main__video-block-item-title">Фильм о компании МЕГАМИКС</div>
        </div>
        <div class="main__video-space"></div>
        <div class="main__video-block-item">
          <iframe width="100%" height="315" src="https://www.youtube.com/embed/LAENAVwtgB8" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          <div class="main__video-block-item-title">Фильм о выращивании растений при помощи удобрений</div>
        </div>
      </div>
    </div>

    <div class="main__block">
      <div class="main__block-title">КУЛЬТУРЫ</div>
      <div class="main__line"></div>
      <div class="main__item-block">
        <div class="veget" v-for="element of veget" :key="element.id">
          <div class="veget-img"><img src="@/assets/veg.png" alt=""></div>
          <div class="veget-name">{{element.name}}</div> 
        </div>
      </div>
    </div>

    <BottomMenu></BottomMenu>
  </div>
</template>

<script>
import HeaderMenu from '@/components/HeaderMenu/HeaderMenu'
import Spinner from '@/components/Spinner/Spinner'
import BottomMenu  from '@/components/BottomMenu/BottomMenu'

export default {
  name: 'Main',
  components: {
    HeaderMenu,
    Spinner,
    BottomMenu
  },
  data () {
    return {
      veget: [
        {
          id: 0,
          name: 'Зерновые'
        },
        {
          id: 1,
          name: 'Подсолнечник'
        },
        {
          id: 2,
          name: 'Кукуруза'
        },
        {
          id: 3,
          name: 'Сахарная свекла'
        },
        {
          id: 4,
          name: 'Картофель'
        },
        {
          id: 5,
          name: 'Зернобобовые'
        },
        {
          id: 6,
          name: 'Рапс'
        },
        {
          id: 7,
          name: 'Лён'
        },
      ]
    }
  }
}
</script>


<style>
  .main{
    position: relative;
    width: 100%;
    color: #4e504f;
  }
  .main__block{
    margin-bottom: 150px;
  }
  .main__block-title{
    font-size: 30px;
    color: #4e504f;
  }
  .main__line{
    position: relative;
    width: 120px;
    border-bottom: 1px solid #4e504f;
    left: 50%;
    margin: 30px 0 10px -60px; 
    margin-bottom: 90px;
  }
  .main__item-block{
    width: 90%;
    height: 100%;
    position: relative;
    left: 0;
    right: 0;
    margin: auto;
  }
  .main__video-block-item{
    width: 45%;
    display: inline-block;
  }
  .main__video-space{
    width: 9%;
    display: inline-block;
  }
  .main__video-block-item-title{
    text-align: left;
    margin-top: 30px;
  }
  .veget{
    position: relative;
    width: 25%;
    display: inline-block;
    margin-bottom: 75px;
  }
  .veget-img{
    width: 80%;
    margin: auto;
    margin-bottom: 25px;
  }
  .veget-name{
    font-size: 20px;
  }
</style>


